import { useState, useCallback, useEffect } from "react";
import { RateTable } from "./RateTable";
import { CurrencyCodePicker } from "./CurrencyCodePicker";
import { AmountField } from "./AmountField";
import { getExchangeRates } from "../api";

const supportedCurrencies = ["CZK", "USD", "EUR"];

export function ExchangeRate() {
  const [amount, setAmount] = useState("1.00");
  const [currencyCode, setCurrencyCode] = useState("USD");
  const [currencyData, setCurrencyData] = useState({ USD: 1.0 });

  // fetch the exchange rates each time currency code changes
  useEffect(() => {
    getExchangeRates(currencyCode, supportedCurrencies).then((rates) => {
      setCurrencyData(rates);
    });
  }, [currencyCode]);

  const handleCurrencyCode = useCallback(
    (e) => setCurrencyCode(e.target.value),
    []
  );

  const handleAmountChange = useCallback((e) => {
    let newAmount = e.target.value;
    setAmount(newAmount);
  }, []);

  return (
    <>
      <section>
        <h1 className="ExchangeRate-header">
          Valyutani Bilish !{" "}
          <CurrencyCodePicker
            supportedCurrencies={supportedCurrencies}
            currencyCode={currencyCode}
            onChange={handleCurrencyCode}
          />
        </h1>
      </section>
      <section>
        <AmountField amount={amount} onChange={handleAmountChange} />
      </section>
      <section>
        <RateTable currencyData={currencyData} amount={amount} />
        <h1>
          Ushbu loyiha Chexiyada ishlab yurgan akalarimiz uchun ishlab chiqildi.
          Bu yerda siz Valyuta kursini ayni vaqtdagi holatini ko'rishingiz
          mumkin!
        </h1>
        <div className="flag">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/cb/Flag_of_the_Czech_Republic.svg/2560px-Flag_of_the_Czech_Republic.svg.png"
            alt="czech"
          />
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/0b/Flag_of_Uzbekistan.png/1200px-Flag_of_Uzbekistan.png"
            alt="uzbek"
          />
        </div>
        <footer className="text">Developed by Abror Umaraliev 2021!</footer>
      </section>
    </>
  );
}
